import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { FrequenciaAtividade } from '../models/frequencia-atividade';

@Injectable({
  providedIn: 'root'
})
export class FrequenciaAtividadeService {
  private apiUrl: string = environment.apiUrl;

  constructor(private http: HttpClient) {}

  public findAll(): Observable<FrequenciaAtividade[]> {
    return this.http.get<FrequenciaAtividade[]>(`${this.apiUrl}/frequencias-atividades`);
  }

  public create(frequenciaAtividade: FrequenciaAtividade): Observable<FrequenciaAtividade> {
    return this.http.post<FrequenciaAtividade>(`${this.apiUrl}/frequencias-atividades`, frequenciaAtividade);
  }

  public update(frequenciaAtividade: FrequenciaAtividade): Observable<FrequenciaAtividade> {
    return this.http.put<FrequenciaAtividade>(`${this.apiUrl}/frequencias-atividades/${frequenciaAtividade.id}`, frequenciaAtividade);
  }

  public delete(frequenciaAtividade: FrequenciaAtividade): Observable<FrequenciaAtividade> {
    return this.http.delete<FrequenciaAtividade>(`${this.apiUrl}/frequencias-atividades/${frequenciaAtividade.id}`);
  }
}
