import { Component, OnDestroy, OnInit } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { AlertHelper } from 'src/app/helpers/alert.helper';
import { GrupoExame } from 'src/app/models/grupo-exame';
import { GrupoExameService } from 'src/app/services/grupo-exame.service';
import { GrupoFormComponent } from './grupo-form/grupo-form.component';

@Component({
  selector: 'app-grupos-de-exames',
  templateUrl: './grupos-de-exames.component.html',
  styleUrls: ['./grupos-de-exames.component.scss']
})
export class GruposDeExamesComponent implements OnInit, OnDestroy {
  public loading: boolean = false;
  public gruposExames?: GrupoExame[];
  public bsModalRef?: BsModalRef;
  private unsubscribe$ = new Subject<void>();

  constructor(
    private grupoExameService: GrupoExameService,
    private modalService: BsModalService,
    private alertHelper: AlertHelper
  ) { }

  public ngOnInit(): void {
    this.loadGruposExames();
  }

  public ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  public openGrupoForm(grupoExame?: GrupoExame): void {
    this.bsModalRef = this.modalService.show(GrupoFormComponent, {
      initialState: { grupoExame }
    });

    const component: GrupoFormComponent = this.bsModalRef.content;

    component.onClose
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (grupoExame) => {
          const index = this.gruposExames.findIndex(g => g.id == grupoExame.id);
          if (index === -1) {
            this.gruposExames.push(grupoExame);
          } else {
            this.gruposExames[index] = grupoExame;
          }
        }
      });
  }

  public deleteGrupoExame(grupoExame: GrupoExame): void {
    this.alertHelper.show(
      'warning', 
      `Tem certeza que deseja remover o grupo de exame ${grupoExame.desc}?`,
      { showCancelButton: true }
    )
    .then(confirmed => {
      if (confirmed) {
        this.loading = true;
        this.grupoExameService.delete(grupoExame)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe({
            next: (grupoExame) => {
              this.loading = false;
              this.gruposExames = this.gruposExames.filter(g => g.id != grupoExame.id);
            },
            error: () => {
              this.loading = false;
            }
          })
      }
    })
  }

  private loadGruposExames(): void {
    this.loading = true;
    this.grupoExameService.findAll()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (gruposExames) => {
          this.loading = false;
          this.gruposExames = gruposExames;
        },
        error: () => {
          this.loading = false;
        }
      });
  }
}
