import { Component, OnDestroy, OnInit } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';
import { Dieta, ItemDieta } from 'src/app/models/dieta';
import { DietaFormComponent } from './dieta-form/dieta-form.component';
import { takeUntil } from 'rxjs/operators';
import { ItemDietaFormComponent } from './item-dieta-form/item-dieta-form.component';
import { RefeicaoService } from 'src/app/services/refeicao.service';
import { ProgramaService } from 'src/app/services/programa.service';
import { Programa } from 'src/app/models/programa';
import { Refeicao } from 'src/app/models/refeicao';
import { DietaService } from 'src/app/services/dieta.service';
import { AlertHelper } from 'src/app/helpers/alert.helper';

@Component({
  selector: 'app-dietas',
  templateUrl: './dietas.component.html',
  styleUrls: ['./dietas.component.scss']
})
export class DietasComponent implements OnInit, OnDestroy {
  public loading: boolean = false;
  public dias: string;
  public dietas: Dieta[];
  public itensDieta: ItemDieta[];
  public bsModalRef?: BsModalRef;
  public programas: Programa[];
  public refeicoes: Refeicao[];
  private unsubscribe$ = new Subject<void>();

  constructor(
    private dietaService: DietaService,
    private modalService: BsModalService,
    private programaService: ProgramaService,
    private refeicaoService: RefeicaoService,
    private alertHelper: AlertHelper
  ) { }

  public ngOnInit(): void {
    this.loadDietas();
    this.loadItensDietas();
    this.loadProgramas();
    this.loadRefeicoes();
  }

  public ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  public openDietaForm(dieta?: Dieta): void {
    this.bsModalRef = this.modalService.show(DietaFormComponent, {
      initialState: {
        dieta,
        itens: this.itensDieta,
        programas: this.programas,
        refeicoes: this.refeicoes,
      }
    });

    const component: DietaFormComponent = this.bsModalRef.content;

    component.onClose
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (dieta) => {
          const index = this.dietas.findIndex(u => u.id == dieta.id);
          if (index === -1) {
            this.dietas.push(dieta);
          } else {
            this.dietas[index] = dieta;
          }
        }
      });
  }

  public openItemDietaForm(item?: ItemDieta): void {
    this.bsModalRef = this.modalService.show(ItemDietaFormComponent, {
      initialState: { item }
    });

    const component: ItemDietaFormComponent = this.bsModalRef.content;

    component.onClose
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (dieta) => {
          const index = this.itensDieta.findIndex(u => u.id == dieta.id);
          if (index === -1) {
            this.itensDieta.push(dieta);
          } else {
            this.itensDieta[index] = dieta;
          }
        }
      });
  }

  public deleteDieta(dieta: Dieta): void {
    this.alertHelper.show(
      'warning',
      `Tem certeza que deseja remover a dieta #${dieta.id}?`,
      { showCancelButton: true }
    )
    .then(confirmed => {
      if (confirmed) {
        this.loading = true;
        this.dietaService.delete(dieta)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe({
            next: (dieta) => {
              this.loading = false;
              this.alertHelper.toast('success', 'Dieta deletada com sucesso.');
              this.dietas = this.dietas.filter(d => d.id != dieta.id);
            },
            error: () => {
              this.loading = false;
            }
          })
      }
    });
  }

  public deleteItemDieta(item: ItemDieta): void {
    this.alertHelper.show(
      'warning',
      `Tem certeza que deseja remover o item ${item.desc}?`,
      { showCancelButton: true }
    )
    .then(confirmed => {
      if (confirmed) {
        this.loading = true;
        this.dietaService.deleteItem(item)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe({
            next: (item) => {
              this.loading = false;
              this.alertHelper.toast('success', 'Item deletado com sucesso.');
              this.itensDieta = this.itensDieta.filter(it => it.id != item.id);
            },
            error: () => {
              this.loading = false;
            }
          })
      }
    });
  }

  private loadDietas(): void {
    this.loading = true;
    this.dietaService.findAll()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (dietas) => {
          this.loading = false;
          this.dietas = dietas;

        },
        error: () => {
          this.loading = false;
        }
      })
  }

  private loadItensDietas(): void {
    this.loading = true;
    this.dietaService.findAllItems()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (dietas) => {
          this.loading = false;
          this.itensDieta = dietas;
        },
        error: () => {
          this.loading = false;
        }
      })
  }

  private loadProgramas(): void {
    this.programaService.findAll()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (programas) => {
          this.programas = programas;
        }
      })
  }

  private loadRefeicoes(): void {
    this.refeicaoService.findAll()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (refeicoes) => {
          this.refeicoes = refeicoes;
        }
      })
  }
}
