import { Component, Input, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { AlertHelper } from 'src/app/helpers/alert.helper';
import { ItemDieta } from 'src/app/models/dieta';
import { ResponseError } from 'src/app/models/response-error';
import { DietaService } from 'src/app/services/dieta.service';

@Component({
  selector: 'app-item-dieta-form',
  templateUrl: './item-dieta-form.component.html',
  styleUrls: ['./item-dieta-form.component.scss']
})
export class ItemDietaFormComponent implements OnInit {
  public loading: boolean = false;
  public desc = new FormControl('', [Validators.required]);
  public urlImg?: string;
  public fileImg?: File;
  public isInvalid: boolean = false;
  public onClose = new Subject<ItemDieta>();
  private unsubscribe$ = new Subject<void>();

  @Input() item?: ItemDieta;

  constructor(
    private bsModalRef: BsModalRef,
    private alertHelper: AlertHelper,
    private dietaService: DietaService
  ) { }

  public ngOnInit(): void {
    if (this.item) {
      this.desc.setValue(this.item.desc);
      this.urlImg = this.item.url_img;
    }
  }

  public ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  public close(): void {
    this.bsModalRef.hide();
  }

  public imageChanged(event: { target: { files: FileList }}) {
    this.fileImg = event.target.files[0];
    const reader = new FileReader();
    reader.onload = () => {
      this.urlImg = reader.result.toString();
       if (this.item?.id) {
        this.uploadImg();
       }
    };
    reader.readAsDataURL(this.fileImg);
  }

  public submit(): void {
    this.isInvalid = this.desc.invalid;

    if (this.item?.id == undefined && this.fileImg == undefined) {
      this.isInvalid = true;
    }

    if (!this.isInvalid) {
      this.item = {
        ...this.item,
        desc: this.desc.value
      };

      if (this.item?.id) {
        this.updateItem();
      } else {
        this.item.file_img = this.fileImg;
        this.createItem();
      }
    }
  }

  private createItem(): void {
    this.loading = true;
    this.dietaService.createItem(this.item)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (item) => {
          this.loading = false;
          this.alertHelper.toast('success', 'Item cadastrado com sucesso.');
          this.onClose.next(item);
          this.bsModalRef.hide();
        },
        error: (e) => {
          this.loading = false;
          if (e instanceof ResponseError) {
            this.alertHelper.show('error', e.getMessage(), {
              confirmButtonText: 'Entendi'
            });
          }
        }
      });
  }

  private updateItem(): void {
    this.loading = true;
    this.dietaService.updateItem(this.item)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (item) => {
          this.loading = false;
          this.alertHelper.toast('success', 'Item atualizado com sucesso.');
          this.onClose.next(item);
          this.bsModalRef.hide();
        },
        error: () => {
          this.loading = false;
        }
      });
  }

  private uploadImg(): void {
    this.loading = true;
    this.dietaService.uploadImg(this.item?.id, this.fileImg)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (item) => {
          this.loading = false;
          this.alertHelper.toast('success', 'Imagem atualizada com sucesso.');
          this.onClose.next(item);
        },
        error: () => {
          this.loading = false;
        }
      });
  }
}
