import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DoencasComponent } from './doencas/doencas.component';
import { IMCComponent } from './imc/imc.component';
import { FrequenciasDeAtividadesComponent } from './frequencias-de-atividades/frequencias-de-atividades.component';
import { GruposDeExamesComponent } from './grupos-de-exames/grupos-de-exames.component';
import { MetasComponent } from './metas/metas.component';
import { RefeicoesComponent } from './refeicoes/refeicoes.component';
import { ProgramasComponent } from './programas/programas.component';
import { ConfiguracoesComponent } from './configuracoes.component';
import { DietasComponent } from './dietas/dietas.component';
import { TreinosComponent } from './treinos/treinos.component';

const routes: Routes = [
  {
    path: '',
    component: ConfiguracoesComponent,
    children: [
      {
        path: 'doencas',
        component: DoencasComponent
      },
      {
        path: 'frequencias-de-atividades',
        component: FrequenciasDeAtividadesComponent
      },
      {
        path: 'grupos-de-exames',
        component: GruposDeExamesComponent
      },
      {
        path: 'imc',
        component: IMCComponent
      },
      {
        path: 'metas',
        component: MetasComponent
      },
      {
        path: 'programas',
        component: ProgramasComponent
      },
      {
        path: 'refeicoes',
        component: RefeicoesComponent
      },
      {
        path: 'dietas',
        component: DietasComponent
      },
      {
        path: 'treinos',
        component: TreinosComponent
      },
      {
        path: '**',
        redirectTo: 'doencas'
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SettingsRoutingModule { }
