import { Component, Input, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { AlertHelper } from 'src/app/helpers/alert.helper';
import { Meta } from 'src/app/models/meta';
import { ResponseError } from 'src/app/models/response-error';
import { MetaService } from 'src/app/services/meta.service';

@Component({
  selector: 'app-meta-form',
  templateUrl: './meta-form.component.html',
  styleUrls: ['./meta-form.component.scss']
})
export class MetaFormComponent implements OnInit {
  public loading: boolean = false;
  public desc = new FormControl('', [Validators.required]);
  public isInvalid: boolean = false;
  public onClose = new Subject<Meta>();
  private unsubscribe$ = new Subject<void>();

  @Input() meta?: Meta;

  constructor(
    private bsModalRef: BsModalRef,
    private alertHelper: AlertHelper,
    private metaService: MetaService
  ) { }

  public ngOnInit(): void {
    if (this.meta) {
      this.desc.setValue(this.meta.desc);
    }
  }

  public ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  public close(): void {
    this.bsModalRef.hide();
  }

  public submit(): void {
    this.isInvalid = this.desc.invalid;

    if (!this.isInvalid) {
      this.meta = {
        ...this.meta,
        desc: this.desc.value,
      };

      if (this.meta?.id) {
        this.updateMeta();
      } else {
        this.createMeta();
      }
    }
  }

  private createMeta(): void {
    this.loading = true;
    this.metaService.create(this.meta)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (meta) => {
          this.loading = false;
          this.alertHelper.toast('success', 'Meta cadastrada com sucesso.');
          this.onClose.next(meta);
          this.bsModalRef.hide();
        },
        error: (e) => {
          this.loading = false;
          if (e instanceof ResponseError) {
            this.alertHelper.show('error', e.getMessage(), {
              confirmButtonText: 'Entendi'
            });
          }
        }
      });
  }

  private updateMeta(): void {
    this.loading = true;
    this.metaService.update(this.meta)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (meta) => {
          this.loading = false;
          this.alertHelper.toast('success', 'Meta atualizada com sucesso.');
          this.onClose.next(meta);
          this.bsModalRef.hide();
        },
        error: () => {
          this.loading = false;
        }
      });
  }
}
