import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Meta } from '../models/meta';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class MetaService {
  private apiUrl: string = environment.apiUrl;

  constructor(private http: HttpClient) {}

  public findAll(): Observable<Meta[]> {
    return this.http.get<Meta[]>(`${this.apiUrl}/metas`);
  }

  public create(meta: Meta): Observable<Meta> {
    return this.http.post<Meta>(`${this.apiUrl}/metas`, meta);
  }

  public update(meta: Meta): Observable<Meta> {
    return this.http.put<Meta>(`${this.apiUrl}/metas/${meta.id}`, meta);
  }

  public delete(meta: Meta): Observable<Meta> {
    return this.http.delete<Meta>(`${this.apiUrl}/metas/${meta.id}`);
  }
}
