import { Component, OnDestroy, OnInit } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { Doenca } from 'src/app/models/doenca';
import { DoencaService } from 'src/app/services/doenca.service';
import { DoencaFormComponent } from './doenca-form/doenca-form.component';
import { AlertHelper } from 'src/app/helpers/alert.helper';

@Component({
  selector: 'app-doencas',
  templateUrl: './doencas.component.html',
  styleUrls: ['./doencas.component.scss']
})
export class DoencasComponent implements OnInit, OnDestroy {
  public loading: boolean = false;
  public doencas?: Doenca[];
  public bsModalRef?: BsModalRef;
  private unsubscribe$ = new Subject<void>();

  constructor(
    private doencaService: DoencaService,
    private modalService: BsModalService,
    private alertHelper: AlertHelper
  ) { }

  public ngOnInit(): void {
    this.loadDoencas();
  }

  public ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  public openDoencaForm(doenca?: Doenca): void {
    this.bsModalRef = this.modalService.show(DoencaFormComponent, {
      initialState: { doenca }
    });

    const component: DoencaFormComponent = this.bsModalRef.content;

    component.onClose
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (doenca) => {
          const index = this.doencas.findIndex(u => u.id == doenca.id);
          if (index === -1) {
            this.doencas.push(doenca);
          } else {
            this.doencas[index] = doenca;
          }
        }
      });
  }

  public deleteDoenca(doenca: Doenca): void {
    this.alertHelper.show(
      'warning', 
      `Tem certeza que deseja remover a doena ${doenca.desc}?`,
      { showCancelButton: true }
    )
    .then(confirmed => {
      if (confirmed) {
        this.loading = true;
        this.doencaService.delete(doenca)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe({
            next: (doenca) => {
              this.loading = false;
              this.doencas = this.doencas.filter(d => d.id != doenca.id);
            },
            error: () => {
              this.loading = false;
            }
          })
      }
    })
  }

  private loadDoencas(): void {
    this.loading = true;
    this.doencaService.findAll()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (doencas) => {
          this.loading = false;
          this.doencas = doencas;
        },
        error: () => {
          this.loading = false;
        }
      });
  }
}
