import { Component, OnDestroy, OnInit } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';
import { AlertHelper } from 'src/app/helpers/alert.helper';
import { FrequenciaAtividade } from 'src/app/models/frequencia-atividade';
import { FrequenciaAtividadeService } from 'src/app/services/frequencia-atividade.service';
import { FrequenciaFormComponent } from './frequencia-form/frequencia-form.component';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-frequencias-de-atividades',
  templateUrl: './frequencias-de-atividades.component.html',
  styleUrls: ['./frequencias-de-atividades.component.scss']
})
export class FrequenciasDeAtividadesComponent implements OnInit, OnDestroy {
  public loading: boolean = false;
  public frequenciasAtividades?: FrequenciaAtividade[];
  public bsModalRef?: BsModalRef;
  private unsubscribe$ = new Subject<void>();

  constructor(
    private frequenciaAtividadeService: FrequenciaAtividadeService,
    private modalService: BsModalService,
    private alertHelper: AlertHelper
  ) { }

  public ngOnInit(): void {
    this.loadFrequenciasAtividades();
  }

  public ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  public openFrequenciaForm(frequenciaAtividade?: FrequenciaAtividade): void {
    this.bsModalRef = this.modalService.show(FrequenciaFormComponent, {
      initialState: { frequenciaAtividade }
    });

    const component: FrequenciaFormComponent = this.bsModalRef.content;

    component.onClose
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (frequenciaAtividade) => {
          const index = this.frequenciasAtividades.findIndex(f => f.id == frequenciaAtividade.id);
          if (index === -1) {
            this.frequenciasAtividades.push(frequenciaAtividade);
          } else {
            this.frequenciasAtividades[index] = frequenciaAtividade;
          }
        }
      });
  }

  public deleteFrequenciaAtividade(frequenciaAtividade: FrequenciaAtividade): void {
    this.alertHelper.show(
      'warning', 
      `Tem certeza que deseja remover a frequncia de atividade ${frequenciaAtividade.desc}?`,
      { showCancelButton: true }
    )
    .then(confirmed => {
      if (confirmed) {
        this.loading = true;
        this.frequenciaAtividadeService.delete(frequenciaAtividade)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe({
            next: (frequenciaAtividade) => {
              this.loading = false;
              this.frequenciasAtividades = this.frequenciasAtividades.filter(f => f.id != frequenciaAtividade.id);
            },
            error: () => {
              this.loading = false;
            }
          })
      }
    })
  }

  private loadFrequenciasAtividades(): void {
    this.loading = true;
    this.frequenciaAtividadeService.findAll()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (FrequenciasAtividades) => {
          this.loading = false;
          this.frequenciasAtividades = FrequenciasAtividades;
        },
        error: () => {
          this.loading = false;
        }
      });
  }
}
