import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Dieta, ItemDieta } from '../models/dieta';

@Injectable({
  providedIn: 'root',
})
export class DietaService {
  private apiUrl: string = environment.apiUrl;

  constructor(private http: HttpClient) {}

  public findAll(): Observable<Dieta[]> {
    return this.http.get<Dieta[]>(`${this.apiUrl}/dietas`);
  }

  public create(dieta: Dieta): Observable<Dieta> {
    return this.http.post<Dieta>(`${this.apiUrl}/dietas`, dieta);
  }

  public update(dieta: Dieta): Observable<Dieta> {
    return this.http.put<Dieta>(`${this.apiUrl}/dietas/${dieta.id}`, dieta);
  }

  public delete(dieta: Dieta): Observable<Dieta> {
    return this.http.delete<Dieta>(`${this.apiUrl}/dietas/${dieta.id}`);
  }

  public findAllItems(): Observable<ItemDieta[]> {
    return this.http.get<ItemDieta[]>(`${this.apiUrl}/itens-dietas`);
  }

  public createItem(item: ItemDieta): Observable<ItemDieta> {
    const formData = new FormData();
    formData.append('desc', item.desc);
    formData.append('file_img', item.file_img);
    return this.http.post<ItemDieta>(`${this.apiUrl}/itens-dietas`, formData);
  }

  public updateItem(item: ItemDieta): Observable<ItemDieta> {
    return this.http.put<ItemDieta>(`${this.apiUrl}/itens-dietas/${item.id}`, item);
  }

  public deleteItem(item: ItemDieta): Observable<ItemDieta> {
    return this.http.delete<ItemDieta>(`${this.apiUrl}/itens-dietas/${item.id}`);
  }

  public uploadImg(itemId: number, fileImg: File): Observable<ItemDieta> {
    const formData = new FormData();
    formData.append('id', itemId.toString());
    formData.append('file_img', fileImg);
    return this.http.post<ItemDieta>(`${this.apiUrl}/itens-dietas/img`, formData);
  }
}
