import { Component, OnInit } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { AlertHelper } from 'src/app/helpers/alert.helper';
import { Programa } from 'src/app/models/programa';
import { ProgramaService } from 'src/app/services/programa.service';
import { ProgramaFormComponent } from './programa-form/programa-form.component';

@Component({
  selector: 'app-programas',
  templateUrl: './programas.component.html',
  styleUrls: ['./programas.component.scss']
})
export class ProgramasComponent implements OnInit {
  public loading: boolean = false;
  public programas?: Programa[];
  public bsModalRef?: BsModalRef;
  private unsubscribe$ = new Subject<void>();

  constructor(
    private programaService: ProgramaService,
    private modalService: BsModalService,
    private alertHelper: AlertHelper
  ) { }

  public ngOnInit(): void {
    this.loadProgramas();
  }

  public ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  public openProgramaForm(programa?: Programa): void {
    this.bsModalRef = this.modalService.show(ProgramaFormComponent, {
      initialState: { programa }
    });

    const component: ProgramaFormComponent = this.bsModalRef.content;

    component.onClose
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (programa) => {
          const index = this.programas.findIndex(m => m.id == programa.id);
          if (index === -1) {
            this.programas.push(programa);
          } else {
            this.programas[index] = programa;
          }
        }
      });
  }

  public deletePrograma(programa: Programa): void {
    this.alertHelper.show(
      'warning',
      `Tem certeza que deseja remover o programa ${programa.desc}?`,
      { showCancelButton: true }
    )
    .then(confirmed => {
      if (confirmed) {
        this.loading = true;
        this.programaService.delete(programa)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe({
            next: (programa) => {
              this.loading = false;
              this.programas = this.programas.filter(m => m.id != programa.id);
            },
            error: () => {
              this.loading = false;
            }
          })
      }
    });
  }

  private loadProgramas(): void {
    this.loading = true;
    this.programaService.findAll()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (programas) => {
          this.loading = false;
          this.programas = programas;
        },
        error: () => {
          this.loading = false;
        }
      });
  }
}
