import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Refeicao } from '../models/refeicao';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class RefeicaoService {
  private apiUrl: string = environment.apiUrl;

  constructor(private http: HttpClient) {}

  public findAll(): Observable<Refeicao[]> {
    return this.http.get<Refeicao[]>(`${this.apiUrl}/refeicoes`);
  }

  public create(refeicao: Refeicao): Observable<Refeicao> {
    return this.http.post<Refeicao>(`${this.apiUrl}/refeicoes`, refeicao);
  }

  public update(refeicao: Refeicao): Observable<Refeicao> {
    return this.http.put<Refeicao>(`${this.apiUrl}/refeicoes/${refeicao.id}`, refeicao);
  }

  public delete(refeicao: Refeicao): Observable<Refeicao> {
    return this.http.delete<Refeicao>(`${this.apiUrl}/refeicoes/${refeicao.id}`);
  }
}
