import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Doenca } from '../models/doenca';

@Injectable({
  providedIn: 'root',
})
export class DoencaService {
  private apiUrl: string = environment.apiUrl;

  constructor(private http: HttpClient) {}

  public findAll(): Observable<Doenca[]> {
    return this.http.get<Doenca[]>(`${this.apiUrl}/doencas`);
  }

  public create(doenca: Doenca): Observable<Doenca> {
    return this.http.post<Doenca>(`${this.apiUrl}/doencas`, doenca);
  }

  public update(doenca: Doenca): Observable<Doenca> {
    return this.http.put<Doenca>(`${this.apiUrl}/doencas/${doenca.id}`, doenca);
  }

  public delete(doenca: Doenca): Observable<Doenca> {
    return this.http.delete<Doenca>(`${this.apiUrl}/doencas/${doenca.id}`);
  }
}
