import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Treino, ItemTreino } from '../models/treino';

@Injectable({
  providedIn: 'root',
})
export class TreinoService {
  private apiUrl: string = environment.apiUrl;

  constructor(private http: HttpClient) {}

  public findAll(): Observable<Treino[]> {
    return this.http.get<Treino[]>(`${this.apiUrl}/treinos`);
  }

  public create(treino: Treino): Observable<Treino> {
    return this.http.post<Treino>(`${this.apiUrl}/treinos`, treino);
  }

  public update(treino: Treino): Observable<Treino> {
    return this.http.put<Treino>(`${this.apiUrl}/treinos/${treino.id}`, treino);
  }

  public delete(treino: Treino): Observable<Treino> {
    return this.http.delete<Treino>(`${this.apiUrl}/treinos/${treino.id}`);
  }

  public findAllItems(): Observable<ItemTreino[]> {
    return this.http.get<ItemTreino[]>(`${this.apiUrl}/itens-treinos`);
  }

  public createItem(item: ItemTreino): Observable<ItemTreino> {
    const formData = new FormData();
    formData.append('desc', item.desc);
    formData.append('file_img', item.file_img);
    formData.append('metodologia', item.metodologia);
    formData.append('intervalo', item.intervalo);
    return this.http.post<ItemTreino>(`${this.apiUrl}/itens-treinos`, formData);
  }

  public updateItem(item: ItemTreino): Observable<ItemTreino> {
    return this.http.put<ItemTreino>(`${this.apiUrl}/itens-treinos/${item.id}`, item);
  }

  public deleteItem(item: ItemTreino): Observable<ItemTreino> {
    return this.http.delete<ItemTreino>(`${this.apiUrl}/itens-treinos/${item.id}`);
  }

  public uploadImg(itemId: number, fileImg: File): Observable<ItemTreino> {
    const formData = new FormData();
    formData.append('id', itemId.toString());
    formData.append('file_img', fileImg);
    return this.http.post<ItemTreino>(`${this.apiUrl}/itens-treinos/img`, formData);
  }
}
