import { Component, OnDestroy, OnInit } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { AlertHelper } from 'src/app/helpers/alert.helper';
import { IMC } from 'src/app/models/imc';
import { IMCService } from 'src/app/services/imc.service';
import { IMCFormComponent } from './imc-form/imc-form.component';

@Component({
  selector: 'app-imc',
  templateUrl: './imc.component.html',
  styleUrls: ['./imc.component.scss']
})
export class IMCComponent implements OnInit, OnDestroy {
  public loading: boolean = false;
  public imcArr?: IMC[];
  public bsModalRef?: BsModalRef;
  private unsubscribe$ = new Subject<void>();

  constructor(
    private imcService: IMCService,
    private modalService: BsModalService,
    private alertHelper: AlertHelper
  ) { }

  public ngOnInit(): void {
    this.loadImcArr();
  }

  public ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  public openImcForm(imc?: IMC): void {
    this.bsModalRef = this.modalService.show(IMCFormComponent, {
      initialState: { imc }
    });

    const component: IMCFormComponent = this.bsModalRef.content;

    component.onClose
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (imc) => {
          const index = this.imcArr.findIndex(u => u.id == imc.id);
          if (index === -1) {
            this.imcArr.push(imc);
          } else {
            this.imcArr[index] = imc;
          }
        }
      });
  }

  public deleteImc(imc: IMC): void {
    this.alertHelper.show(
      'warning',
      `Tem certeza que deseja remover o IMC ${imc.tipo}?`,
      { showCancelButton: true }
    )
    .then(confirmed => {
      if (confirmed) {
        this.loading = true;
        this.imcService.delete(imc)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe({
            next: (imc) => {
              this.loading = false;
              this.imcArr = this.imcArr.filter(d => d.id != imc.id);
            },
            error: () => {
              this.loading = false;
            }
          })
      }
    });
  }

  private loadImcArr(): void {
    this.loading = true;
    this.imcService.findAll()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (imcArr) => {
          this.loading = false;
          this.imcArr = imcArr;
        },
        error: () => {
          this.loading = false;
        }
      });
  }
}
