import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { AlertHelper } from 'src/app/helpers/alert.helper';
import { GrupoExame } from 'src/app/models/grupo-exame';
import { ResponseError } from 'src/app/models/response-error';
import { GrupoExameService } from 'src/app/services/grupo-exame.service';

@Component({
  selector: 'app-grupo-form',
  templateUrl: './grupo-form.component.html',
  styleUrls: ['./grupo-form.component.scss']
})
export class GrupoFormComponent implements OnInit, OnDestroy {
  public loading: boolean = false;
  public desc = new FormControl('', [Validators.required]);
  public isInvalid: boolean = false;
  public onClose = new Subject<GrupoExame>();
  private unsubscribe$ = new Subject<void>();

  @Input() grupoExame?: GrupoExame;

  constructor(
    private bsModalRef: BsModalRef,
    private alertHelper: AlertHelper,
    private grupoExameService: GrupoExameService
  ) { }

  public ngOnInit(): void {
    if (this.grupoExame) {
      this.desc.setValue(this.grupoExame.desc);
    }
  }

  public ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  public close(): void {
    this.bsModalRef.hide();
  }

  public submit(): void {
    this.isInvalid = this.desc.invalid;

    if (!this.isInvalid) {
      this.grupoExame = {
        ...this.grupoExame,
        desc: this.desc.value,
      };

      if (this.grupoExame?.id) {
        this.updateGrupoExame();
      } else {
        this.createGrupoExame();
      }
    }
  }

  private createGrupoExame(): void {
    this.loading = true;
    this.grupoExameService.create(this.grupoExame)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (grupoExame) => {
          this.loading = false;
          this.alertHelper.toast('success', 'Grupo de exame cadastrado com sucesso.');
          this.onClose.next(grupoExame);
          this.bsModalRef.hide();
        },
        error: (e) => {
          this.loading = false;
          if (e instanceof ResponseError) {
            this.alertHelper.show('error', e.getMessage(), {
              confirmButtonText: 'Entendi'
            });
          }
        }
      });
  }

  private updateGrupoExame(): void {
    this.loading = true;
    this.grupoExameService.update(this.grupoExame)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (grupoExame) => {
          this.loading = false;
          this.alertHelper.toast('success', 'Grupo de exame atualizado com sucesso.');
          this.onClose.next(grupoExame);
          this.bsModalRef.hide();
        },
        error: () => {
          this.loading = false;
        }
      });
  }
}
