import { Component, Input, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { AlertHelper } from 'src/app/helpers/alert.helper';
import { Refeicao } from 'src/app/models/refeicao';
import { ResponseError } from 'src/app/models/response-error';
import { RefeicaoService } from 'src/app/services/refeicao.service';

@Component({
  selector: 'app-refeicao-form',
  templateUrl: './refeicao-form.component.html',
  styleUrls: ['./refeicao-form.component.scss']
})
export class RefeicaoFormComponent implements OnInit {
  public loading: boolean = false;
  public desc = new FormControl('', [Validators.required]);
  public isInvalid: boolean = false;
  public onClose = new Subject<Refeicao>();
  private unsubscribe$ = new Subject<void>();

  @Input() refeicao?: Refeicao;

  constructor(
    private bsModalRef: BsModalRef,
    private alertHelper: AlertHelper,
    private refeicaoService: RefeicaoService
  ) { }

  public ngOnInit(): void {
    if (this.refeicao) {
      this.desc.setValue(this.refeicao.desc);
    }
  }

  public ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  public close(): void {
    this.bsModalRef.hide();
  }

  public submit(): void {
    this.isInvalid = this.desc.invalid;

    if (!this.isInvalid) {
      this.refeicao = {
        ...this.refeicao,
        desc: this.desc.value,
      };

      if (this.refeicao?.id) {
        this.updateRefeicao();
      } else {
        this.createRefeicao();
      }
    }
  }

  private createRefeicao(): void {
    this.loading = true;
    this.refeicaoService.create(this.refeicao)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (refeicao) => {
          this.loading = false;
          this.alertHelper.toast('success', 'Refeicao cadastrado com sucesso.');
          this.onClose.next(refeicao);
          this.bsModalRef.hide();
        },
        error: (e) => {
          this.loading = false;
          if (e instanceof ResponseError) {
            this.alertHelper.show('error', e.getMessage(), {
              confirmButtonText: 'Entendi'
            });
          }
        }
      });
  }

  private updateRefeicao(): void {
    this.loading = true;
    this.refeicaoService.update(this.refeicao)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (refeicao) => {
          this.loading = false;
          this.alertHelper.toast('success', 'Refeicao atualizado com sucesso.');
          this.onClose.next(refeicao);
          this.bsModalRef.hide();
        },
        error: () => {
          this.loading = false;
        }
      });
  }
}
