import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Programa } from '../models/programa';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ProgramaService {
  private apiUrl: string = environment.apiUrl;

  constructor(private http: HttpClient) {}

  public findAll(): Observable<Programa[]> {
    return this.http.get<Programa[]>(`${this.apiUrl}/programas`);
  }

  public create(programa: Programa): Observable<Programa> {
    return this.http.post<Programa>(`${this.apiUrl}/programas`, programa);
  }

  public update(programa: Programa): Observable<Programa> {
    return this.http.put<Programa>(`${this.apiUrl}/programas/${programa.id}`, programa);
  }

  public delete(programa: Programa): Observable<Programa> {
    return this.http.delete<Programa>(`${this.apiUrl}/programas/${programa.id}`);
  }
}
