import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { AlertHelper } from 'src/app/helpers/alert.helper';
import { Doenca } from 'src/app/models/doenca';
import { ResponseError } from 'src/app/models/response-error';
import { DoencaService } from 'src/app/services/doenca.service';

@Component({
  selector: 'app-doenca-form',
  templateUrl: './doenca-form.component.html',
  styleUrls: ['./doenca-form.component.scss']
})
export class DoencaFormComponent implements OnInit, OnDestroy {
  public loading: boolean = false;
  public desc = new FormControl('', [Validators.required]);
  public isInvalid: boolean = false;
  public onClose = new Subject<Doenca>();
  private unsubscribe$ = new Subject<void>();

  @Input() doenca?: Doenca;

  constructor(
    private bsModalRef: BsModalRef,
    private alertHelper: AlertHelper,
    private doencaService: DoencaService
  ) { }

  public ngOnInit(): void {
    if (this.doenca) {
      this.desc.setValue(this.doenca.desc);
    }
  }

  public ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  public close(): void {
    this.bsModalRef.hide();
  }

  public submit(): void {
    this.isInvalid = this.desc.invalid;

    if (!this.isInvalid) {
      this.doenca = {
        ...this.doenca,
        desc: this.desc.value,
      };

      if (this.doenca?.id) {
        this.updateDoenca();
      } else {
        this.createDoenca();
      }
    }
  }

  private createDoenca(): void {
    this.loading = true;
    this.doencaService.create(this.doenca)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (doenca) => {
          this.loading = false;
          this.alertHelper.toast('success', 'Doena cadastrada com sucesso.');
          this.onClose.next(doenca);
          this.bsModalRef.hide();
        },
        error: (e) => {
          this.loading = false;
          if (e instanceof ResponseError) {
            this.alertHelper.show('error', e.getMessage(), {
              confirmButtonText: 'Entendi'
            });
          }
        }
      });
  }

  private updateDoenca(): void {
    this.loading = true;
    this.doencaService.update(this.doenca)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (doenca) => {
          this.loading = false;
          this.alertHelper.toast('success', 'Doena atualizada com sucesso.');
          this.onClose.next(doenca);
          this.bsModalRef.hide();
        },
        error: () => {
          this.loading = false;
        }
      });
  }
}
