import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { GrupoExame } from '../models/grupo-exame';

@Injectable({
  providedIn: 'root'
})
export class GrupoExameService {
  private apiUrl: string = environment.apiUrl;

  constructor(private http: HttpClient) {}

  public findAll(): Observable<GrupoExame[]> {
    return this.http.get<GrupoExame[]>(`${this.apiUrl}/grupos-exames`);
  }

  public create(grupoExame: GrupoExame): Observable<GrupoExame> {
    return this.http.post<GrupoExame>(`${this.apiUrl}/grupos-exames`, grupoExame);
  }

  public update(grupoExame: GrupoExame): Observable<GrupoExame> {
    return this.http.put<GrupoExame>(`${this.apiUrl}/grupos-exames/${grupoExame.id}`, grupoExame);
  }

  public delete(grupoExame: GrupoExame): Observable<GrupoExame> {
    return this.http.delete<GrupoExame>(`${this.apiUrl}/grupos-exames/${grupoExame.id}`);
  }
}
