import { Component, OnDestroy, OnInit } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';
import { AlertHelper } from 'src/app/helpers/alert.helper';
import { Refeicao } from 'src/app/models/refeicao';
import { RefeicaoService } from 'src/app/services/refeicao.service';
import { RefeicaoFormComponent } from './refeicao-form/refeicao-form.component';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-refeicoes',
  templateUrl: './refeicoes.component.html',
  styleUrls: ['./refeicoes.component.scss']
})
export class RefeicoesComponent implements OnInit, OnDestroy {
  public loading: boolean = false;
  public refeicoes?: Refeicao[];
  public bsModalRef?: BsModalRef;
  private unsubscribe$ = new Subject<void>();

  constructor(
    private refeicaoService: RefeicaoService,
    private modalService: BsModalService,
    private alertHelper: AlertHelper
  ) { }

  public ngOnInit(): void {
    this.loadRefeicoes();
  }

  public ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  public openRefeicaoForm(refeicao?: Refeicao): void {
    this.bsModalRef = this.modalService.show(RefeicaoFormComponent, {
      initialState: { refeicao }
    });

    const component: RefeicaoFormComponent = this.bsModalRef.content;

    component.onClose
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (refeicao) => {
          const index = this.refeicoes.findIndex(m => m.id == refeicao.id);
          if (index === -1) {
            this.refeicoes.push(refeicao);
          } else {
            this.refeicoes[index] = refeicao;
          }
        }
      });
  }

  public deleteRefeicao(refeicao: Refeicao): void {
    this.alertHelper.show(
      'warning',
      `Tem certeza que deseja remover a refeio ${refeicao.desc}?`,
      { showCancelButton: true }
    )
    .then(confirmed => {
      if (confirmed) {
        this.loading = true;
        this.refeicaoService.delete(refeicao)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe({
            next: (refeicao) => {
              this.loading = false;
              this.refeicoes = this.refeicoes.filter(m => m.id != refeicao.id);
            },
            error: () => {
              this.loading = false;
            }
          })
      }
    });
  }

  private loadRefeicoes(): void {
    this.loading = true;
    this.refeicaoService.findAll()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (refeicoes) => {
          this.loading = false;
          this.refeicoes = refeicoes;
        },
        error: () => {
          this.loading = false;
        }
      });
  }
}
