import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { AlertHelper } from 'src/app/helpers/alert.helper';
import { IMC } from 'src/app/models/imc';
import { ResponseError } from 'src/app/models/response-error';
import { IMCService } from 'src/app/services/imc.service';

@Component({
  selector: 'app-imc-form',
  templateUrl: './imc-form.component.html',
  styleUrls: ['./imc-form.component.scss']
})
export class IMCFormComponent implements OnInit, OnDestroy {
  public loading: boolean = false;
  public tipo = new FormControl('', [Validators.required]);
  public isInvalid: boolean = false;
  public onClose = new Subject<IMC>();
  private unsubscribe$ = new Subject<void>();

  @Input() imc?: IMC;

  constructor(
    private bsModalRef: BsModalRef,
    private alertHelper: AlertHelper,
    private imcService: IMCService
  ) { }

  public ngOnInit(): void {
    if (this.imc) {
      this.tipo.setValue(this.imc.tipo);
    }
  }

  public ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  public close(): void {
    this.bsModalRef.hide();
  }

  public submit(): void {
    this.isInvalid = this.tipo.invalid;

    if (!this.isInvalid) {
      this.imc = {
        ...this.imc,
        tipo: this.tipo.value,
      };

      if (this.imc?.id) {
        this.updateImc();
      } else {
        this.createImc();
      }
    }
  }

  private createImc(): void {
    this.loading = true;
    this.imcService.create(this.imc)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (imc) => {
          this.loading = false;
          this.alertHelper.toast('success', 'IMC cadastrado com sucesso.');
          this.onClose.next(imc);
          this.bsModalRef.hide();
        },
        error: (e) => {
          this.loading = false;
          if (e instanceof ResponseError) {
            this.alertHelper.show('error', e.getMessage(), {
              confirmButtonText: 'Entendi'
            });
          }
        }
      });
  }

  private updateImc(): void {
    this.loading = true;
    this.imcService.update(this.imc)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (imc) => {
          this.loading = false;
          this.alertHelper.toast('success', 'IMC atualizado com sucesso.');
          this.onClose.next(imc);
          this.bsModalRef.hide();
        },
        error: () => {
          this.loading = false;
        }
      });
  }
}
