import { Component, Input, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { AlertHelper } from 'src/app/helpers/alert.helper';
import { FrequenciaAtividade } from 'src/app/models/frequencia-atividade';
import { ResponseError } from 'src/app/models/response-error';
import { FrequenciaAtividadeService } from 'src/app/services/frequencia-atividade.service';

@Component({
  selector: 'app-frequencia-form',
  templateUrl: './frequencia-form.component.html',
  styleUrls: ['./frequencia-form.component.scss']
})
export class FrequenciaFormComponent implements OnInit {
  public loading: boolean = false;
  public desc = new FormControl('', [Validators.required]);
  public isInvalid: boolean = false;
  public onClose = new Subject<FrequenciaAtividade>();
  private unsubscribe$ = new Subject<void>();

  @Input() frequenciaAtividade?: FrequenciaAtividade;

  constructor(
    private bsModalRef: BsModalRef,
    private alertHelper: AlertHelper,
    private frequenciaAtividadeService: FrequenciaAtividadeService
  ) { }

  public ngOnInit(): void {
    if (this.frequenciaAtividade) {
      this.desc.setValue(this.frequenciaAtividade.desc);
    }
  }

  public ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  public close(): void {
    this.bsModalRef.hide();
  }

  public submit(): void {
    this.isInvalid = this.desc.invalid;

    if (!this.isInvalid) {
      this.frequenciaAtividade = {
        ...this.frequenciaAtividade,
        desc: this.desc.value,
      };

      if (this.frequenciaAtividade?.id) {
        this.updateFrequenciaAtividade();
      } else {
        this.createFrequenciaAtividade();
      }
    }
  }

  private createFrequenciaAtividade(): void {
    this.loading = true;
    this.frequenciaAtividadeService.create(this.frequenciaAtividade)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (frequenciaAtividade) => {
          this.loading = false;
          this.alertHelper.toast('success', 'Frequncia de atividade cadastrada com sucesso.');
          this.onClose.next(frequenciaAtividade);
          this.bsModalRef.hide();
        },
        error: (e) => {
          this.loading = false;
          if (e instanceof ResponseError) {
            this.alertHelper.show('error', e.getMessage(), {
              confirmButtonText: 'Entendi'
            });
          }
        }
      });
  }

  private updateFrequenciaAtividade(): void {
    this.loading = true;
    this.frequenciaAtividadeService.update(this.frequenciaAtividade)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (frequenciaAtividade) => {
          this.loading = false;
          this.alertHelper.toast('success', 'Frequncia de atividade atualizada com sucesso.');
          this.onClose.next(frequenciaAtividade);
          this.bsModalRef.hide();
        },
        error: () => {
          this.loading = false;
        }
      });
  }
}
