import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { IMC } from '../models/imc';

@Injectable({
  providedIn: 'root'
})
export class IMCService {
  private apiUrl: string = environment.apiUrl;

  constructor(private http: HttpClient) {}

  public findAll(): Observable<IMC[]> {
    return this.http.get<IMC[]>(`${this.apiUrl}/imc`);
  }

  public create(imc: IMC): Observable<IMC> {
    return this.http.post<IMC>(`${this.apiUrl}/imc`, imc);
  }

  public update(imc: IMC): Observable<IMC> {
    return this.http.put<IMC>(`${this.apiUrl}/imc/${imc.id}`, imc);
  }

  public delete(imc: IMC): Observable<IMC> {
    return this.http.delete<IMC>(`${this.apiUrl}/imc/${imc.id}`);
  }
}
