import { Component, OnInit } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';
import { AlertHelper } from 'src/app/helpers/alert.helper';
import { Meta } from 'src/app/models/meta';
import { MetaService } from 'src/app/services/meta.service';
import { MetaFormComponent } from './meta-form/meta-form.component';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-metas',
  templateUrl: './metas.component.html',
  styleUrls: ['./metas.component.scss']
})
export class MetasComponent implements OnInit {
  public loading: boolean = false;
  public metas?: Meta[];
  public bsModalRef?: BsModalRef;
  private unsubscribe$ = new Subject<void>();

  constructor(
    private metaService: MetaService,
    private modalService: BsModalService,
    private alertHelper: AlertHelper
  ) { }

  public ngOnInit(): void {
    this.loadMetas();
  }

  public ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  public openMetaForm(meta?: Meta): void {
    this.bsModalRef = this.modalService.show(MetaFormComponent, {
      initialState: { meta }
    });

    const component: MetaFormComponent = this.bsModalRef.content;

    component.onClose
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (meta) => {
          const index = this.metas.findIndex(m => m.id == meta.id);
          if (index === -1) {
            this.metas.push(meta);
          } else {
            this.metas[index] = meta;
          }
        }
      });
  }

  public deleteMeta(meta: Meta): void {
    this.alertHelper.show(
      'warning',
      `Tem certeza que deseja remover a meta ${meta.desc}?`,
      { showCancelButton: true }
    )
    .then(confirmed => {
      if (confirmed) {
        this.loading = true;
        this.metaService.delete(meta)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe({
            next: (meta) => {
              this.loading = false;
              this.metas = this.metas.filter(m => m.id != meta.id);
            },
            error: () => {
              this.loading = false;
            }
          })
      }
    });
  }

  private loadMetas(): void {
    this.loading = true;
    this.metaService.findAll()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (metas) => {
          this.loading = false;
          this.metas = metas;
        },
        error: () => {
          this.loading = false;
        }
      });
  }
}
