import { Component, OnDestroy, OnInit } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';
import { Treino, ItemTreino } from 'src/app/models/treino';
import { TreinoFormComponent } from './treino-form/treino-form.component';
import { takeUntil } from 'rxjs/operators';
import { ItemTreinoFormComponent } from './item-treino-form/item-treino-form.component';
import { RefeicaoService } from 'src/app/services/refeicao.service';
import { ProgramaService } from 'src/app/services/programa.service';
import { Programa } from 'src/app/models/programa';
import { AlertHelper } from 'src/app/helpers/alert.helper';
import { TreinoService } from 'src/app/services/treino.service';

@Component({
  selector: 'app-treinos',
  templateUrl: './treinos.component.html',
  styleUrls: ['./treinos.component.scss']
})
export class TreinosComponent implements OnInit, OnDestroy {
  public loading: boolean = false;
  public treinos: Treino[];
  public itensTreino: ItemTreino[];
  public bsModalRef?: BsModalRef;
  public programas: Programa[];
  private unsubscribe$ = new Subject<void>();

  constructor(
    private treinoService: TreinoService,
    private modalService: BsModalService,
    private programaService: ProgramaService,
    private refeicaoService: RefeicaoService,
    private alertHelper: AlertHelper
  ) { }

  public ngOnInit(): void {
    this.loadTreinos();
    this.loadItensTreinos();
    this.loadProgramas();
  }

  public ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  public openTreinoForm(treino?: Treino): void {
    this.bsModalRef = this.modalService.show(TreinoFormComponent, {
      initialState: {
        treino,
        itens: this.itensTreino,
        programas: this.programas,
      }
    });

    const component: TreinoFormComponent = this.bsModalRef.content;

    component.onClose
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (treino) => {
          const index = this.treinos.findIndex(u => u.id == treino.id);
          if (index === -1) {
            this.treinos.push(treino);
          } else {
            this.treinos[index] = treino;
          }
        }
      });
  }

  public openItemTreinoForm(item?: ItemTreino): void {
    this.bsModalRef = this.modalService.show(ItemTreinoFormComponent, {
      initialState: { item }
    });

    const component: ItemTreinoFormComponent = this.bsModalRef.content;

    component.onClose
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (treino) => {
          const index = this.itensTreino.findIndex(u => u.id == treino.id);
          if (index === -1) {
            this.itensTreino.push(treino);
          } else {
            this.itensTreino[index] = treino;
          }
        }
      });
  }

  public deleteTreino(treino: Treino): void {
    this.alertHelper.show(
      'warning',
      `Tem certeza que deseja remover a treino #${treino.id}?`,
      { showCancelButton: true }
    )
    .then(confirmed => {
      if (confirmed) {
        this.loading = true;
        this.treinoService.delete(treino)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe({
            next: (treino) => {
              this.loading = false;
              this.alertHelper.toast('success', 'Treino deletado com sucesso.');
              this.treinos = this.treinos.filter(d => d.id != treino.id);
            },
            error: () => {
              this.loading = false;
            }
          })
      }
    });
  }

  public deleteItemTreino(item: ItemTreino): void {
    this.alertHelper.show(
      'warning',
      `Tem certeza que deseja remover o item ${item.desc}?`,
      { showCancelButton: true }
    )
    .then(confirmed => {
      if (confirmed) {
        this.loading = true;
        this.treinoService.deleteItem(item)
          .pipe(takeUntil(this.unsubscribe$))
          .subscribe({
            next: (item) => {
              this.loading = false;
              this.alertHelper.toast('success', 'Item deletado com sucesso.');
              this.itensTreino = this.itensTreino.filter(it => it.id != item.id);
            },
            error: () => {
              this.loading = false;
            }
          })
      }
    });
  }

  private loadTreinos(): void {
    this.loading = true;
    this.treinoService.findAll()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (treinos) => {
          this.loading = false;
          this.treinos = treinos;
        },
        error: () => {
          this.loading = false;
        }
      })
  }

  private loadItensTreinos(): void {
    this.loading = true;
    this.treinoService.findAllItems()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (treinos) => {
          this.loading = false;
          this.itensTreino = treinos;
        },
        error: () => {
          this.loading = false;
        }
      })
  }

  private loadProgramas(): void {
    this.programaService.findAll()
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (programas) => {
          this.programas = programas;
        }
      })
  }
}
