import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { AlertHelper } from 'src/app/helpers/alert.helper';
import { Programa } from 'src/app/models/programa';
import { ResponseError } from 'src/app/models/response-error';
import { ProgramaService } from 'src/app/services/programa.service';

@Component({
  selector: 'app-programa-form',
  templateUrl: './programa-form.component.html',
  styleUrls: ['./programa-form.component.scss']
})
export class ProgramaFormComponent implements OnInit, OnDestroy {
  public loading: boolean = false;
  public desc = new FormControl('', [Validators.required]);
  public isInvalid: boolean = false;
  public onClose = new Subject<Programa>();
  private unsubscribe$ = new Subject<void>();

  @Input() programa?: Programa;

  constructor(
    private bsModalRef: BsModalRef,
    private alertHelper: AlertHelper,
    private programaService: ProgramaService
  ) { }

  public ngOnInit(): void {
    if (this.programa) {
      this.desc.setValue(this.programa.desc);
    }
  }

  public ngOnDestroy(): void {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  public close(): void {
    this.bsModalRef.hide();
  }

  public submit(): void {
    this.isInvalid = this.desc.invalid;

    if (!this.isInvalid) {
      this.programa = {
        ...this.programa,
        desc: this.desc.value,
      };

      if (this.programa?.id) {
        this.updatePrograma();
      } else {
        this.createPrograma();
      }
    }
  }

  private createPrograma(): void {
    this.loading = true;
    this.programaService.create(this.programa)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (programa) => {
          this.loading = false;
          this.alertHelper.toast('success', 'Programa cadastrado com sucesso.');
          this.onClose.next(programa);
          this.bsModalRef.hide();
        },
        error: (e) => {
          this.loading = false;
          if (e instanceof ResponseError) {
            this.alertHelper.show('error', e.getMessage(), {
              confirmButtonText: 'Entendi'
            });
          }
        }
      });
  }

  private updatePrograma(): void {
    this.loading = true;
    this.programaService.update(this.programa)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe({
        next: (programa) => {
          this.loading = false;
          this.alertHelper.toast('success', 'Programa atualizado com sucesso.');
          this.onClose.next(programa);
          this.bsModalRef.hide();
        },
        error: () => {
          this.loading = false;
        }
      });
  }
}
